* {
    font-family: "Microsoft YaHei", 微软雅黑, "MicrosoftJhengHei", 华文细黑, STHeiti, MingLiu
}
.win10-full{
    position: absolute;
    left: 0px;
    top:0;
    right: 0;
    bottom: 0;
}
#statusbar-display {display:none !important;
}
/*磁贴自定义样式*/
.win10-block-content-text {
    line-height: 44px;
    text-align: center;
    font-size: 16px;
}
#win10-menu .blocks {
    max-width: 650px;
    width: calc(100% - 240px);
}
.fade-enter-active, .fade-leave-active {
    -webkit-transition: all .5s;
    transition: all .5s;
}
.fade-enter-active{
    height: 0px;
    visibility: visible;
}
#win10_command_center.hidden-command-center{
    right: -350px;
}
.fade-enter-active.fade-enter-to{
    height: 40px;
}
.fade-leave-active{
    height: 40px;
}
.fade-leave-to{
    height: 0px;
}
#win10-menu .list .sub-item{
    width: auto;
}
#win10-menu .list .item, .sub-item{
    width: auto;
}
body{
     margin: 0;
     padding: 0;
}
.wrapp{
    position: absolute;
    top: 50%;
    left: 50%;
    width: 600px;
    height: 300px;
    margin: -150px 0 0 -300px;
    -webkit-perspective: 30px;
    -webkit-transform: translateX(0px);
    -webkit-animation: wrapp 400ms 800ms ease-in forwards;
    -moz-perspective: 30px;
    -moz-transform: translateX(0px);
    -moz-animation: wrapp 400ms 800ms ease-in forwards;
    -ms-perspective: 30px;
    -ms-transform: translateX(0px);
    -ms-animation: wrapp 400ms 800ms ease-in forwards;
    perspective: 30px;
    transform: translateX(0px);
    animation: wrapp 400ms 800ms ease-in forwards;
}
.text{
    position: absolute;
    top: 50%;
    left: 50%;
    width: 0px;
    height: 60px;
    margin: -30px 0 0 -160px;
    overflow: hidden;
    -webkit-transform: translateX(0px);
    -webkit-animation: text 400ms 800ms linear forwards;
    -moz-transform: translateX(0px);
    -moz-animation: text 400ms 800ms linear forwards;
    -ms-transform: translateX(0px);
    -ms-animation: text 400ms 800ms linear forwards;
    transform: translateX(0px);
    animation: text 400ms 800ms linear forwards;
}
h1{
    float: right;
    font-family: "Segoe UI", Frutiger, "Frutiger Linotype", "Dejavu Sans", "Helvetica Neue", Arial, sans-serif;
    font-weight: normal;
    color: #fff;
    padding: 0;
    margin: 0;
    width: 320px;
    height: 60px;
    font-size: 60px;
    line-height: 60px;
}
.logo{
    position: absolute;
    top: 50%;
    left: 50%;
    width: 90px;
    height: 90px;
    margin: -45px 0 0 -45px;
    background-color: #fff;
    -webkit-transform: translateX(0px) rotateY(10deg);
    -webkit-animation: logo 500ms 300ms ease-out forwards;
    -moz-transform: translateX(0px) rotateY(10deg);
    -moz-animation: logo 500ms 300ms ease-out forwards;
    -ms-transform: translateX(0px) rotateY(10deg);
    -ms-animation: logo 500ms 300ms ease-out forwards;
    transform: translateX(0px) rotateY(10deg);
    animation: logo 500ms 300ms ease-out forwards;
}
.logo .top-left{
    position: absolute;
    top: 0;
    left: 0;
    width: 44px;
    height: 44px;
    border-right: solid 2px #90da15;
    border-bottom: solid 2px #90da15;
}
.logo .bottom-right{
    position: absolute;
    bottom: 0;
    right: 0;
    width: 44px;
    height: 44px;
    border-left: solid 2px #90da15;
    border-top: solid 2px #90da15;
}
@-webkit-keyframes logo {
from {
                 -webkit-transform: translateX(0px) rotateY(10deg);
}
to {
                 -webkit-transform: translateX(0px) rotateY(-10deg);
}
}
@-webkit-keyframes text {
from {
        width: 0px;
        -webkit-transform: translateX(0px);
}
60%{
        width: 0px;
}
to {
        width: 320px;
        -webkit-transform: translateX(240px);
}
}
@-webkit-keyframes wrapp {
from {
        -webkit-transform: translateX(0px);
}
to {
        -webkit-transform: translateX(-200px);
}
}
@keyframes logo {
from {
        -webkit-transform: translateX(0px) rotateY(10deg);
                transform: translateX(0px) rotateY(10deg);
}
to {
        -webkit-transform: translateX(0px) rotateY(-10deg);
                transform: translateX(0px) rotateY(-10deg);
}
}
@keyframes text {
from {
        width: 0px;
        -webkit-transform: translateX(0px);
                transform: translateX(0px);
}
60%{
        width: 0px;
}
to {
        width: 320px;
        -webkit-transform: translateX(240px);
                transform: translateX(240px);
}
}
@keyframes wrapp {
from {
        -webkit-transform: translateX(0px);
                transform: translateX(0px);
}
to {
        -webkit-transform: translateX(-200px);
                transform: translateX(-200px);
}
}
































































#win10-login {
    background: url('/static/img/wallpapers/login.jpg') no-repeat fixed;
    right: 0;
    bottom: 0;
    background-size: 100% 100%;
    position: fixed;
    top: 0;
    left: 0;
}
#win10-login-box {
    width: 300px;
    overflow: hidden;
    margin: 0 auto;
}
.win10-login-box-square {
    width: 105px;
    margin: 0 auto;
    border-radius: 50%;
    background-color: darkgray;
    position: relative;
    overflow: hidden;
}
.win10-login-box-square::after {
    content: "";
    display: block;
    padding-bottom: 100%;
}
.win10-login-box-square .content {
    position: absolute;
    width: 100%;
    height: 100%;
}
input {
    width: 90%;
    display: block;
    border: 0;
    margin: 0 auto;
    line-height: 36px;
    font-size: 20px;
    padding: 0 1em;
    border-radius: 5px;
    margin-bottom: 11px;
}
.login-username, .login-password {
    width: 91%;
    font-size: 13px;
    color: #999;
}
.login-password {
    width: calc(91% - 54px);
    border-radius: 5px 0 0 5px;
    margin: 0px;
    float: left;
}
.login-submit {
    margin: 0px;
    float: left;
    border-radius: 0 5px 5px 0;
    background-color: #009688;
    width: 54px;
    display: inline-block;
    height: 36px;
    line-height: 36px;
    padding: 0 auto;
    color: #fff;
    white-space: nowrap;
    text-align: center;
    font-size: 14px;
    border: none;
    cursor: pointer;
    opacity: .9;
    filter: alpha(opacity=90);
}
